<template>
    <div class="default-panel prolist-panel" id="proList">
        <proheaders></proheaders>
        <div class="pd30 bg-sec">
            <div class="pro-img-swiper">
                <mt-swipe :auto="4000">
                    <mt-swipe-item class="pro-slide">
                        <img :src="productDetail.detailImg1" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img :src="productDetail.detailImg2" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img :src="productDetail.detailImg3" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img :src="productDetail.detailImg4" alt="">
                    </mt-swipe-item>
                </mt-swipe>
            </div>
            
            <div class="pro-titles">
                <h1>{{currSkuBean.displayName}}</h1>
                <p class="text-red">¥{{currSkuBean.price | filterPrice}}</p>
                <p class="text-grey">门市价: ¥{{currSkuBean.originalPrice | filterPrice }}</p>
            </div>
            <!-- 规格 tlp start -->
            <div class="pro-spec">
                <p class="text-grey">选择规格</p>
                <div class="spec-list">
                    <span class="item-spec" :class="{'active': activeSpec == index}" v-for="(item,index) in productSkuList" @click="getCurrSku(index)">
                        {{item.productSkuBeanVo.size}}
                    </span>
                </div>
                <div class="spec-details">
                    <ul class="spec-details-list">
                        <li v-for="item in currSkuSize">{{item.specKey}}：{{item.specValue}}</li>
                    </ul>
                </div>
            </div>
            <!-- 规格 tlp end -->
            <!-- 选择数量 tlp end -->
            <div class="counter-operate">
                <span class="text-grey">选择数量</span>
                <span>
                    <span class="icon-reduce" @click="reduce"></span>
                    <span class="text-counter">{{counter}}</span>
                    <span class="icon-plus" @click="plus"></span>
                </span>
            </div>
            <!-- 选择数量 tlp end -->
            <div class="pro-help">
                <span class="item-help" @click="promiseShow=true">幸福承诺</span><span class="item-help">查看配送区域</span>
            </div>
        </div>
        <div class="pd30 bg-sec">
            <div class="pro-details">
                <h3 class="title">产品详情</h3>
                <div class="pro-details-info">
                    <img src="http://qn.blissmall.net/o_1b004qnh13aa1skj1dpueeen7e160.jpg" alt="">
                </div>
            </div>
        </div>

        <div class="pro-footer">
            <span class="i-cart" @click="addToCart">加入购物车</span>
            <span class="i-buynow">立即购买</span>
        </div>

        <mt-popup
          v-model="promiseShow"
          position = 'bottom'
          class="promise-popup"
          > 
            <span class="promise-btn" @click="promiseShow=false">我知道了</span>
            <div class="promise-sec">
                <p class="title">幸福承诺</p>
                <p class="promise-line"><span class="i-b">就敢退</span> 实物不对版，退款不退货！</p>
                <p class="promise-line"><span class="i-b">就敢减</span> 约定时间！迟到1分钟减1元！</p>   
                <p class="promise-line"><span class="i-b">就敢送</span> 迟到30分钟免费赠送！</p>
            </div>
        </mt-popup>

    </div>
</template>
<script>
    import Vue from 'vue';
    import { Swipe, SwipeItem } from 'mint-ui';
    import { Popup } from 'mint-ui';
    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);
    Vue.component(Popup.name, Popup);

    import proheaders from '@/components/home/productDetail/proDetailHeader'


    // 注册
    Vue.filter('filterPrice', function (value) {
        return (value/100).toFixed(2);
    })

    export default {
        data(){
            return {
                promiseShow : false,
                activeSpec: 0,
                currSkuBean: {},//当前所选规格信息
                currSkuSize: {},//当前所选规格尺寸信息
                counter: 1,//数量
            }
        },
        components: {
            proheaders
        },
        computed: {
            productSkuList(){
                return this.$store.state.proDetails.productSkuDetailList
            },
            productDetail(){
                return this.$store.state.proDetails.productDetail
            }
        },
        mounted(){
            this.getProInfo();
            this.currSkuBean = this.productSkuList[0].productSkuBeanVo;
            this.currSkuSize = this.productSkuList[0].productSkuSizeSpecList; 

        },
        methods: {
            getProInfo(){
                var res = {
                    data: {
                        "productBeanVo": {
                            "categoryId": 29,
                            "id": 100012,
                            "name": "芒果沙滩"
                        },
                        "productDetailVo": {
                            "consistency": 0,
                            "detailImg1": "http://qn.blissmall.net/o_1b00208o01i6v13tr6st1so01e05vl.jpg?imageslim",
                            "detailImg2": "http://qn.blissmall.net/o_1b00208o01i6v13tr6st1so01e05vl.jpg?imageslim",
                            "detailImg3": "http://qn.blissmall.net/o_1b00208o01i6v13tr6st1so01e05vl.jpg?imageslim",
                            "detailImg4": "http://qn.blissmall.net/o_1b00208o01i6v13tr6st1so01e05vl.jpg?imageslim",
                            "detailTemplateCopy": "原味蛋糕胚，两层栗子茸夹心，芒果浪花与栗子沙滩浪漫相遇！",
                            "detailTemplateCopyEnd": "",
                            "detailTopCopy": "原味蛋糕胚，两层栗子茸夹心，芒果浪花与栗子沙滩浪漫相遇！",
                            "listCopy": "原味蛋糕胚，两层栗子茸夹心，芒果浪花与栗子沙滩浪漫相遇！",
                            "listImg": "http://rs.blissmall.net/o_1b004rrfa10uuspq1id11i703h616d.jpg",
                            "productId": 100012,
                            "regionalismId": 440300
                        },
                        "productSkuDetailVoList": [
                            {
                                "productSkuBeanVo": {
                                    "displayName": "芒果沙滩蛋糕",
                                    "id": 102939,
                                    "originalPrice": 25900,
                                    "price": 15900,
                                    "size": "约2磅"
                                },
                                "productSkuSizeSpecList": [
                                    {
                                        "sizeId": 1,
                                        "specKey": "方形尺寸",
                                        "specValue": "约18X18cm"
                                    },
                                    {
                                        "sizeId": 2,
                                        "specKey": "重量",
                                        "specValue": "约900g"
                                    },
                                    {
                                        "sizeId": 2,
                                        "specKey": "餐具",
                                        "specValue": "赠送1刀＋10盘＋10叉＋10湿巾"
                                    }
                                ]
                            },
                            {
                                "productSkuBeanVo": {
                                    "displayName": "芒果沙滩蛋糕",
                                    "id": 102939,
                                    "originalPrice": 25800,
                                    "price": 15800,
                                    "size": "约3磅"
                                },
                                "productSkuSizeSpecList": [
                                    {
                                        "sizeId": 2,
                                        "specKey": "圆形尺寸",
                                        "specValue": "约9英寸（直径约22cm）"
                                    }
                                ]
                            },
                            {
                                "productSkuBeanVo": {
                                    "displayName": "芒果沙滩蛋糕",
                                    "id": 102939,
                                    "originalPrice": 25700,
                                    "price": 15700,
                                    "size": "约4磅"
                                },
                                "productSkuSizeSpecList": [
                                    {
                                        "sizeId": 3,
                                        "specKey": "重量",
                                        "specValue": "约900g"
                                    }
                                ]
                            }
                        ]
                    },
                    mate: {

                    }
                };
                this.$store.dispatch('getDetails',res.data)
            },
            getCurrSku(index){
                this.activeSpec = index;
                this.currSkuBean = this.productSkuList[index].productSkuBeanVo;
                this.currSkuSize = this.productSkuList[index].productSkuSizeSpecList;
            },
            reduce(){
                if(this.counter <= 1){
                    return false;
                }else {
                    this.counter --;
                }
            },
            plus(){
                this.counter ++;
            },
            addToCart(){
                this.$store.dispatch('addCart',1)
            }

        }

    }
</script>

<style lang="scss">
    $baseFontSize: 750px;
    @function pxToRem($px) {
        @return $px / $baseFontSize * 10rem;
    }

    .prolist-panel {
        background-color: #eee;
    }

    .bg-sec {
        background-color: #fff;
    }

    .page-infinite-listitem {
        height: 150px;
        line-height: 150px;
    }

    .pro-img-swiper {
        width: 100%;
        height: pxToRem(750px);
        .pro-slide {
            img {
                max-width: 100%;
                min-height: 100%;
            }
        }
        .mint-swipe-indicator {
            background-color: rgba(255,255,255,1);
            opacity: 0.5;
            transform: scale(.8);
            &.is-active{
                background-color: rgba(255,255,255,1);
                opacity: 1;
                transform: scale(1);
            }
        }
    }

    .pro-titles {
        padding: pxToRem(60px) 0;
        text-align: center;
        border-bottom: 1px solid #ccc;
        h1 {
            font-size: pxToRem(28px);
            font-weight: normal;
            line-height: 1;
        }
        .text-red ,.text-grey {
            margin-top: pxToRem(20px);
            line-height: 1;
        }
        .text-grey {
            font-size: pxToRem(20px);
        }

    }

    .pro-spec {
        padding: pxToRem(60px) pxToRem(10px) 0 pxToRem(10px);

        .spec-list {
            margin-top: pxToRem(20px);
            font-size: 0;
            
            .item-spec {
                display: inline-block;
                width: pxToRem(140px);
                height: pxToRem(60px);
                line-height: pxToRem(60px);
                text-align: center;
                font-size: pxToRem(26px);
                border: 1px solid #666;
                &:not(:last-of-type) {
                    margin-right: pxToRem(36px);
                }
                &.active {
                    background-color: #D8331D;
                    color: #fff;
                    border-color: #D8331D;
                }
            }
        }

        .spec-details {
            margin-top: pxToRem(40px);
            padding: pxToRem(20px) pxToRem(16px) pxToRem(60px) pxToRem(16px);
            color: #666666;
            border-top: 1px solid #EEEEEE;

            .spec-details-list {
                font-size: 0;
                li {
                    padding: pxToRem(10px);
                    display: inline-block;
                    font-size: pxToRem(20px);
                    vertical-align: top;
                    width: 50%;
                }
            }
        }
    }

    .counter-operate {
        padding: pxToRem(40px) pxToRem(10px);
        display: flex;
        justify-content: space-between;
        line-height: pxToRem(40px);
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;

        .text-counter {
            margin: 0 pxToRem(40px);
        }

    }
    
    .pro-help {
        padding: pxToRem(40px) pxToRem(10px); 
        display: flex;
        justify-content: space-between;

        .item-help {
            border-bottom: 1px solid #191919;
        }
    }
    
    .pro-details {
        margin-top: pxToRem(60px);
        padding-top: pxToRem(80px);
        text-align: center;

        .title {
            font-size: pxToRem(24px);
            font-weight: normal;
        }

        .pro-details-info {
            margin-top: pxToRem(58px);
            padding-bottom: pxToRem(80px);

            img {
                width: 100%;
                max-width: 100%;
            }
        }
    }

    .pro-footer {
        position: fixed;
        display: flex;
        width: 100%;
        bottom: 0;
        left: 0;
        text-align: center;
        .i-cart,.i-buynow {
            flex: 1;
            line-height: pxToRem(110px);
            color: #fff;
            font-size: pxToRem(32px);
        }
        .i-cart {
            background-color: #D4B675;
        }
        .i-buynow {
            background-color: #D8331D;
        }
    }

    .promise-popup {
        width: 100%;
        text-align: center;
        background-color: #EEEEEE;

        .promise-sec {
            padding: pxToRem(40px) 0;
            .promise-line {
                padding: pxToRem(16px) 0;
                line-height: 1;
            }

            .i-b {
                font-weight: bold;
            }
        }

        .title {
            padding: pxToRem(16px) 0;
            font-size: pxToRem(28px);
        }

        .promise-btn {
            padding: pxToRem(36px) 0;
            display: inline-block;
            width: 100%;
            background-color: #fff;

        }

    }
</style>


