export const ADDTOCART = 'ADDTOCART';


export default {
	state: {
		counter: 0,//购物车商品数
	},
	actions: {
		addCart({ commit },counter,id){
			commit(ADDTOCART,{
				counter:counter
			});
		}
	},
	mutations:{
		[ADDTOCART]( state, options){
			state.counter += options.counter;
		}
	}
}


