export const LOGIN = 'LOGIN'
export const SETREFRESHTOKEN = 'setRefreshToken'
export const SETTOKEN = 'setToken'
export const SETCITY = 'setUserCity'
export default {
  state: {
    userName: '',
    token: '',
    refreshToken: '',
    headImgURL: '',
    isSignSuccess: false,
    //currentCity里面数据属性
    //            "name": "佛山市"
    //            "cityId": 440600,
    //            "cityName": "佛山市",
    //            "cityNamePingyin": "fo shan shi",
    //            "isCity": 1,
    //            "provinceId": 440000,
    //            "provinceName": "广东省",
    //            "regionalismId": 440600,
    //            "regionalismName": "佛山市",
    //            "regionalismNamePingyin": "fo shan shi"
    currentCity: {cityId:'440300', id:"440300", name:"深圳市", cityName:"深圳市", provinceName: "广东省"}
  },
  actions: {
    setUserLogin ({ commit }, loginInfo) {
      commit(LOGIN, loginInfo)
    },
    setRefreshToken ({ commit }, value) {
      commit(SETREFRESHTOKEN, value)
    },
    setToken ({ commit }, value) {
      commit(SETTOKEN, value)
    },
    setUserCity ({ commit }, value) {
      commit(SETCITY, value)
    }
  },
  mutations: {
    [LOGIN] (state, loginInfo) {
      state.userName = loginInfo['userName']
      state.token = loginInfo['token']
      state.refreshToken = loginInfo['refreshToken']
      state.headImgURL = loginInfo['headImgURL']
      state.isSignSuccess = loginInfo['isSignSuccess']
    },
    [SETREFRESHTOKEN] (state, value) {
      state.refreshToken = value
    },
    [SETTOKEN] (state, value) {
      state.token = value
    },
    [SETCITY] (state, value) {
      state.currentCity = value
    }
  },
  getters: {
    getIsSignSuccess (state) {
      return state.isSignSuccess
    }
  }
}



