	export default {
		data(){
			return {

			}
		},
		computed: mapState({
			navList: state => state.homeState.navList,
		}),
		mounted(){
			this.getNavList();
		},
		methods:{
			getNavList(){
				var res = {
				    "data": {
				        "0": [
				            {
				                "id": 26,
				                "name": "蛋糕",
				                "type": 1
				            },
				            {
				                "id": 33,
				                "name": "下午茶",
				                "type": 2
				            },
				            {
				                "id": 34,
				                "name": "手信",
				                "type": 3
				            }
				        ],
				        "1": [
				            {
				                "id": 27,
				                "name": "混合型蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 28,
				                "name": "鲜果蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 29,
				                "name": "巧克力蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 30,
				                "name": "芝士慕斯蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 31,
				                "name": "多层蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 32,
				                "name": "节日蛋糕",
				                "parentId": 26
				            },
				            {
				                "id": 39,
				                "name": "前台不显示",
				                "parentId": 26
				            }
				        ],
				        "2": [
				            {
				                "id": 35,
				                "name": "下午茶系列",
				                "parentId": 33
				            },
				            {
				                "id": 45,
				                "name": "test",
				                "parentId": 33
				            }
				        ],
				        "3": [
				            {
				                "id": 36,
				                "name": "全国包邮",
				                "parentId": 34
				            }
				        ]
				    },
				    "meta": {
				        "code": "00000",
				        "msg": "ok",
				        "success": true
				    }
				}

				this.$store.dispatch('getNavList',res.data);
			},
			goNav(id,parentId){
				this.$store.dispatch('navShowToggle');
				this.$store.dispatch('getCategory',{id,parentId});
				this.$store.dispatch('getClassifyProList');
			}
		}
	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.right-nav-wap {
		padding: pxToRem(100px) pxToRem(30px) pxToRem(110px) 0;
		display: inline-block;
		width: pxToRem(558px);
		height: 100%;
		background: #fff;
		top: 50%;
	    right: 0;
	    bottom: auto;
	    left: auto;
	    -webkit-transform: translate3d(0,-50%,0);
	    transform: translate3d(0,-50%,0);
	    opacity: .96;

	}

	.right-nav-content {
		padding-top: pxToRem(120px);
		width: 100%;
		height: 100%;
		background-color: #eeeeee;

		.nav-list {
			width: 100%;
			font-size: pxToRem(28px);

			li {
				.nav-item {
					padding: pxToRem(18px) 0 pxToRem(18px) pxToRem(140px);
					font-weight: bold;
					&:active {
						background-color: #fff;
					}
				}
			}
			.sub-nav-list {
				margin-bottom: pxToRem(45px);
				font-size: pxToRem(24px);
				li {
					padding: pxToRem(18px) 0 pxToRem(18px)  pxToRem(164px);
