import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'
import Root from '../components/common/root/root'
import Home from '../components/home/home/home'
import ShoppingCart from '../components/shoppingCart/shoppingCart/shoppingCart'
import Mine from '../components/mine/mine/mine'
import ProductDetail from '../components/home/productDetail/productDetail'
import ChooseCity from '../components/common/chooseCity/chooseCity'
import ProductList from '@/components/home/home/productList'
// import Test from '@/components/test'
// import login from '../login'
Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Root,
      children: [
        {
          path: '/',
          component: Home
        },
        {
          path: '/ShoppingCart',
          component: ShoppingCart
        },
        {
          path: '/Mine',
          component: Mine
        },
        {
          path: '/Home',
          component: Home
        }
      ]
    },
    {
      path: '/productList',
      name: 'ProductList',
      component: ProductList
    },
    {
      path: '/productdetail/:id',
      name: 'ProductDetail',
      component: ProductDetail
    },
    {
      path: '/chooseCity',
      name: 'chooseCity',
      component: ChooseCity
    }
    // {
    //   path: '/test',
    //   name: 'test',
    //   component: Test
    // }

  ]
})



