<template>
	<div class="detailsHeader">
		<span><span class="icon-closed"></span></span>
		<div class="details-title">
			<span class="adress-adress-group">
				<span class="vt-middle">幸福西饼--呵呵呵</span>
			</span>
		</div>
		<span class="icon-cart-group">
			<span class="icon-cart">{{counter}}</span>
		</span>
	</div>
</template>
<script>
	import { mapState } from 'vuex'
	export default {
		data(){
			return {

			}
		},
		computed: mapState({
		    counter: state => state.shopCart.counter
  		})

	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.detailsHeader {
		padding: 0 pxToRem(30px);
		display: flex;
		justify-content: space-between;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
		background-color: #fff;
		opacity: .9;
	}
	.details-title {

	}
	.icon-cart-group {
		display: flex;
		align-items: center;
		.icon-cart {
			text-align: center;
		    line-height: pxToRem(40px);
		    color: #666;
		    font-size: pxToRem(20px);
		}
	}
</style>


